import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Http, Headers, RequestOptions } from '@angular/http';
import {ServiceUrl} from './ServiceUrl';

@Injectable()

export class SignupService {
    serviceUrl = new ServiceUrl();
    constructor(private http: Http) {
        console.log("Sign Up initialized");
    }

    addUser(name, email, userToBeCreatedRole, createdByUserId,token) {
        let body = JSON.stringify({ "userDisplayName": name, "email": email, "userToBeCreatedRole": userToBeCreatedRole, "createdByUserId": createdByUserId,"token": token});
        let headers = new Headers({ 'Content-Type': 'application/json', "Access-Control-Allow-Origin": "*" });
        let options = new RequestOptions({ method: 'post', headers: headers });
        console.log(body);
        return this.http.post(this.serviceUrl.baseUrl + "users/addAdminsRoute", body, options)
            .map(res => res.json());
    }

    addAgency(agencyName, name, email, userToBeCreatedRole, createdByUserId,token) {
        let body = JSON.stringify({ "agencyName": agencyName, "userDisplayName": name, "email": email, "userToBeCreatedRole": userToBeCreatedRole, "createdByUserId": createdByUserId,"token": token});
        let headers = new Headers({ 'Content-Type': 'application/json', "Access-Control-Allow-Origin": "*" });
        let options = new RequestOptions({ method: 'post', headers: headers });
        console.log(body);
        return this.http.post(this.serviceUrl.baseUrl + "users/addAdminsRoute", body, options)
            .map(res => res.json());
    }
}


